.profile-card {
    display: flex;
    flex: 0 0 auto;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    padding-right: 435px;
    padding-left: 435px;
    padding-top: 50px;
    background: #FFFFFF;
  }
  .greeting-heading {
    flex: 0 0 auto;
    font: 500 24px Inter;
    color: #000000;
    white-space: pre-wrap;
  }
  .user-profile {
    display: flex;
    flex: 0 0 auto;
    flex-direction: column;
    align-items: stretch;
    align-self: stretch;
    justify-content: flex-start;
    margin-top: 87px;
  }
  .personal-info-container {
    display: flex;
    flex: 0 0 auto;
    flex-direction: column;
    align-items: stretch;
    justify-content: flex-start;
  }
  .info-card {
    display: flex;
    flex: 0 0 auto;
    flex-direction: row;
    gap: 8px;
    align-items: center;
    justify-content: space-between;
  }
  .name-text {
    flex: 0 0 auto;
    font: 500 11px Inter;
    color: #000000;
    white-space: pre-wrap;
  }
  .name-paragraph {
    flex: 0 0 auto;
    font: 400 11px Inter;
    color: #000000;
    white-space: pre-wrap;
  }
  .horizontal-divider {
    box-sizing: border-box;
    flex: 0 0 auto;
    width: 100%;
    height: 2px;
    background: #E8E8E8;
    margin-top: 11px;
  }
  .account-actions {
    display: flex;
    flex: 0 0 auto;
    flex-direction: column;
    align-items: stretch;
    justify-content: flex-start;
    margin-top: 195px;
  }
  .edit-button {
    flex: 0 0 auto;
    font: 400 13px Inter;
    color: #000000;
    text-align: center;
    white-space: pre-wrap;
  }
  .logout-button {
    flex: 0 0 auto;
    margin-top: 3px;
    font: 500 13px Inter;
    color: rgba(238, 52, 101, 1);
    text-align: center;
    white-space: pre-wrap;
  }
  
@media (max-width: 980px) {
  .profile-card {
      margin: 212px 179px 0;
  }

  .greeting-heading {
      font-size: 24px;
  }

  .account-actions {
      margin-top: 200px;
  }

  .user-profile {
      margin-top: 72px;
  }
}

@media (max-width: 740px) {
  .profile-card {
      margin: 70px 30px 0;
  }

  .greeting-heading {
      margin: 0;
  }

  .user-profile {
      margin-top: 80px;
  }

  .account-actions {
      margin: 379px auto 40px;
  }
}